export const partnerShopApi = {
  /**
   * ãƒ‘ãƒ¼ãƒˆãƒŠãƒ¼ä¿®ç†åº—ä¸€è¦§ã‚’å–å¾—
   */
  getShops: async (
    status?: string,
    prefecture?: string,
    specialty?: string
  ): Promise<PartnerShop[]> => {
    try {
      const params: any = {};
      if (status) params.status = status;
      if (prefecture) params.prefecture = prefecture;
      if (specialty) params.specialty = specialty;

      const response = await apiClient.get<{
        shops: PartnerShop[];
        count: number;
      }>('/api/v1/partner-shops', { params });

      return response.data.shops || [];
    } catch (error: any) {
      console.error('ãƒ‘ãƒ¼ãƒˆãƒŠãƒ¼ä¿®ç†åº—ä¸€è¦§å–å¾—ã‚¨ãƒ©ãƒ¼:', error);
      // æ¥ç¶šã‚¨ãƒ©ãƒ¼ã®å ´åˆã¯ã‚ˆã‚Šè©³ç´°ãªæƒ…å ±ã‚’æä¾›
      if (error.code === 'ECONNREFUSED' || error.message?.includes('Network Error')) {
        const connectionError = new Error('ãƒãƒƒã‚¯ã‚¨ãƒ³ãƒ‰ã‚µãƒ¼ãƒãƒ¼ã«æ¥ç¶šã§ãã¾ã›ã‚“ã€‚ã‚µãƒ¼ãƒãƒ¼ãŒèµ·å‹•ã—ã¦ã„ã‚‹ã‹ç¢ºèªã—ã¦ãã ã•ã„ã€‚');
        (connectionError as any).code = error.code;
        throw connectionError;
      }
      throw error;
    }
  },

  /**
   * ãƒ‘ãƒ¼ãƒˆãƒŠãƒ¼ä¿®ç†åº—è©³ç´°ã‚’å–å¾—
   */
  getShop: async (shopId: string): Promise<PartnerShop | null> => {
    try {
      const response = await apiClient.get<PartnerShop>(
        `/api/v1/partner-shops/${shopId}`
      );
      return response.data;
    } catch (error: any) {
      console.error('ãƒ‘ãƒ¼ãƒˆãƒŠãƒ¼ä¿®ç†åº—å–å¾—ã‚¨ãƒ©ãƒ¼:', error);
      return null;
    }
  },
};

export const dealApi = {
  /**
   * å•ã„åˆã‚ã›ãƒ•ã‚©ãƒ¼ãƒ ã‚’é€ä¿¡ï¼ˆå•†è«‡ä½œæˆï¼‰
   */
  submitInquiry: async (formData: InquiryFormData): Promise<Deal> => {
    try {
      const response = await apiClient.post<{
        success: boolean;
        deal: Deal;
        message: string;
      }>('/api/v1/deals', formData);

      if (response.data.success && response.data.deal) {
        return response.data.deal;
      }

      throw new Error(response.data.message || 'å•ã„åˆã‚ã›ã®é€ä¿¡ã«å¤±æ•—ã—ã¾ã—ãŸ');
    } catch (error: any) {
      console.error('å•ã„åˆã‚ã›é€ä¿¡ã‚¨ãƒ©ãƒ¼:', error);
      throw error;
    }
  },

  /**
   * å•†è«‡ä¸€è¦§ã‚’å–å¾—
   */
  getDeals: async (
    status?: string,
    partnerPageId?: string
  ): Promise<Deal[]> => {
    try {
      const params: any = {};
      if (status) params.status = status;
      if (partnerPageId) params.partner_page_id = partnerPageId;

      const response = await apiClient.get<{
        deals: Deal[];
        count: number;
      }>('/api/v1/deals', { params });

      return response.data.deals || [];
    } catch (error: any) {
      console.error('å•†è«‡ä¸€è¦§å–å¾—ã‚¨ãƒ©ãƒ¼:', error);
      throw error;
    }
  },

  /**
   * å•†è«‡ã‚¹ãƒ†ãƒ¼ã‚¿ã‚¹ã‚’æ›´æ–°
   */
  updateStatus: async (
    dealId: string,
    status: string
  ): Promise<Deal> => {
    try {
      const response = await apiClient.patch<{
        success: boolean;
        deal: Deal;
      }>(`/api/v1/deals/${dealId}/status`, { status });

      if (response.data.success && response.data.deal) {
        return response.data.deal;
      }

      throw new Error('ã‚¹ãƒ†ãƒ¼ã‚¿ã‚¹æ›´æ–°ã«å¤±æ•—ã—ã¾ã—ãŸ');
    } catch (error: any) {
      console.error('ã‚¹ãƒ†ãƒ¼ã‚¿ã‚¹æ›´æ–°ã‚¨ãƒ©ãƒ¼:', error);
      throw error;
    }
  },

  /**
   * æˆç´„é‡‘é¡ã‚’æ›´æ–°
   */
  updateAmount: async (
    dealId: string,
    dealAmount: number,
    commissionRate?: number
  ): Promise<Deal> => {
    try {
      const response = await apiClient.patch<{
        success: boolean;
        deal: Deal;
      }>(`/api/v1/deals/${dealId}/amount`, {
        deal_amount: dealAmount,
        commission_rate: commissionRate,
      });

      if (response.data.success && response.data.deal) {
        return response.data.deal;
      }

      throw new Error('æˆç´„é‡‘é¡æ›´æ–°ã«å¤±æ•—ã—ã¾ã—ãŸ');
    } catch (error: any) {
      console.error('æˆç´„é‡‘é¡æ›´æ–°ã‚¨ãƒ©ãƒ¼:', error);
      throw error;
    }
  },
};

