'use client';

import { Message } from '@/types';

interface MessageListProps {
  messages: Message[];
}

export default function MessageList({ messages }: MessageListProps) {
  return (
    <div className="flex flex-col gap-4 p-4 bg-gray-50 rounded-lg border-2 border-gray-200 max-h-[400px] overflow-y-auto">
      {messages.length === 0 ? (
        <div className="text-center text-gray-500 py-8">
          <p className="text-lg">ğŸ†• æ–°ã—ã„ä¼šè©±ã‚’é–‹å§‹ã—ã¾ã—ãŸã€‚ä½•ã§ã‚‚ãŠèããã ã•ã„ï¼</p>
        </div>
      ) : (
        messages.map((message) => {
          // ã‚·ã‚¹ãƒ†ãƒ ãƒ¡ãƒƒã‚»ãƒ¼ã‚¸ã®å ´åˆã¯ä¸­å¤®ã«è¡¨ç¤º
          if (message.sender === 'system') {
            return (
              <div key={message.id} className="flex justify-center my-4">
                <div className="max-w-[90%] bg-yellow-50 border border-yellow-200 text-yellow-800 rounded-lg px-4 py-3 text-center italic">
                  <div className="whitespace-pre-wrap break-words">{message.text}</div>
                </div>
              </div>
            );
          }
          
          return (
            <div
              key={message.id}
              className={`flex ${
                message.sender === 'user' ? 'justify-end' : 'justify-start'
              }`}
            >
              <div
                className={`max-w-[80%] rounded-lg px-4 py-2 ${
                  message.sender === 'user'
                    ? 'bg-gradient-to-r from-purple-600 to-purple-700 text-white'
                    : 'bg-white text-gray-800 border border-gray-200 shadow-sm'
                }`}
              >
                <div className="whitespace-pre-wrap break-words">
                  {message.text}
                </div>
                <div
                  className={`text-xs mt-1 ${
                    message.sender === 'user' ? 'text-purple-100' : 'text-gray-500'
                  }`}
                >
                  {message.timestamp.toLocaleTimeString('ja-JP', {
                    hour: '2-digit',
                    minute: '2-digit',
                  })}
                </div>
              </div>
            </div>
          );
        })
      )}
    </div>
  );
}

